<template>
    <div>
        <form>
            <v-text-field
                v-model="oldPassword"
                label="Old Password"
                type="password"
                required
                outlined
                dense
            ></v-text-field>
            <v-text-field
                v-model="newPassword"
                label="New Password"
                type="password"
                required
                outlined
                dense
            ></v-text-field>
            <CButton @click="update" color="primary" class="px-4">
                Update
                <v-progress-circular
                    v-if="loading"
                    indeterminate
                    color="primary"
                    :size="15"
                ></v-progress-circular>
            </CButton>
        </form>
    </div>
</template>
<script>
//
export default {
    name: "PasswordForm",
    data() {
        return {
            oldPassword: "",
            newPassword: "",
            loading: false
        };
    },
    watch: {},
    methods: {
        update() {
            let self = this;
            self.loading = true;
            let data = {
                old_password: self.oldPassword,
                new_password: self.newPassword
            };
            this.$store
                .dispatch("auth/resetpassword", data)
                .then(response => {
                    self.loading = false;
                })
                .catch(error => {
                    self.loading = false;
                });
        }
    }
};
</script>

